import Vue from 'vue'
import App from './App.vue'
import Axios from 'axios'  //axiosをインポート
import router from './router.js'  //ルーターJSをインポート
import store from './store.js'  //Vuexを設定しているstore.jsをインポート
import VueScrollTo from 'vue-scrollto'  //vue-scrolltoをインポート

//AxiosオブジェクトにbaseURLを設定する
const baseURL = {  //3種類のエンドポイントURLを定義する
  'url01':'http://localhost:3000', //(1)ローカル開発用エンドポイント
  'url02':'http://54.168.208.113:3000', //(2)AWS-EC2のIPアドレスへ直接接続するエンドポイント
  'url03':'https://diary-app.sekiguchiportfolio.net' } //(3)AWS-ELBを介した接続用エンドポイント
Axios.defaults.baseURL = baseURL.url03  //baseURLを設定する（url01、02、03を場合によって切り替える）

Vue.config.productionTip = false

Vue.use(VueScrollTo)  //vue-scrolltoプラグインを適用する

new Vue({
  router,  //インポートしたルーターJSをVueインスタンスに登録
  store,  //Vuexを登録
  render: h => h(App),
}).$mount('#app')
